

import {defineComponent, onMounted} from "vue";

export default defineComponent({
  name: 'InfiniteScroll',

  props: {
    items: Array,
    lastPage: Number,
    page: Number,
  },

  setup(props, {emit}) {
    onMounted(() => {
      emit("loadMoreItems", 1);
    });

    const onIntersect = (isIntersecting: boolean, entries: IntersectionObserverEntry[]) => {
      const page = props.page !== undefined ? props.page : 1;
      const lastPage = props.lastPage !== undefined ? props.lastPage : 0;
      if (entries[0].isIntersecting && page < lastPage) {
        emit('loadMoreItems', page + 1);
      }
    }

    return {
      onIntersect,
    }
  }
})
